// Molecules - Loader

// Default state
.c-loader {
	transition: opacity $transition-duration ease-out, z-index linear, transform $transition-duration ease-out;
	transform: scale(0);
	z-index: -1;
	opacity: 0;
	position: fixed;
	width: 50px;
	height: 50px;
	top: 50%;
	left: 50%;
	margin-left: -25px;
	margin-top: -25px;
}

// When active
body.loading .c-loader {
	transform: scale(1);
	z-index: 10;
	opacity: 1;
}

.c-loader__svg-circle {
	transform: rotate(-90deg);
	transform-origin: 50% 50%;
	stroke-width: 30px;
	stroke: $color-brand;
	fill: transparent;
	stroke-dasharray: 50;
	stroke-dashoffset: 800;
}
