// .wrapper
.o-journey-inner,
.o-journey-inner.hide {
	background: $body-bg url('https://preview.ibb.co/h32upF/sky_background_clouds.jpg') repeat-x;
	opacity: 0;
	text-align: center;
	transition: opacity $transition-duration ease-out, background-position $transition-duration ease-in-out;
	background-position: 50% 120%;

	h1 {
		transition: transform $transition-duration ease-out;
		transform: translate(0, 10px);
	}
}

.o-journey-inner.ready,
.o-journey-inner.show {
	opacity: 1;
	background-position: 50% 100%;

	h1 {
		transform: translate(0, 0);
	}
}

.o-journey-wrapper {
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
